.homepage::-webkit-scrollbar {
	display: none;
}
.homepage {
	//   max-width: 1200px;
	overflow-y: scroll;

	.homepage-main {
		margin: 0 auto;
		flex-direction: column;
		justify-content: space-between;

		.hero {
			margin-top: 50px;
			display: flex;
			gap: 150px;
			align-items: flex-start;
			height: fit-content;
			justify-content: space-between;
			position: relative;

			img {
				height: 43vh;
			}
			.hero-introduce {
				align-self: flex-start;
				height: 100%;
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				align-items: flex-start;
				min-height: 405px;

				.hero-headline {
					font-size: 3.5rem;
					line-height: 50px;
					font-weight: 700;
				}
				.hero-expand {
					margin: 30px 0;
					font-size: 1.5rem;
					line-height: 36px;
					font-weight: 500;
				}
				.taskflow {
					font-style: italic;
				}
			}
			button {
				margin-top: 25px;
				width: 225px;
				height: 50px;
				font-size: 1.5rem;
				border-radius: 8px;
				color: whitesmoke;
				background-color: #0065ff;
				justify-self: flex-end;
				border: none;
			}

			button:hover {
				background-color: rgb(43, 43, 223);
				cursor: pointer;
				transition: ease-in-out 0.5;
			}
		}

		h1 {
			margin: 75px 0;
			font-size: 3rem;
		}

		.website-preview {
			.img-div {
				height: 800px;
				width: 1200px;
			}
			img {
				width: 1200px;
				margin-bottom: 32px;
			}
			// text-align: center;
		}
	}
}
