.login-signup {
	display: flex;
	justify-content: center;
	height: 95vh;
	width: 100%;

	background: linear-gradient(1deg, #fff, #eae6ff 80%);
	background-image: linear-gradient(1deg, rgb(255, 255, 255), rgb(234, 230, 255) 80%);
	background-position-x: initial;
	background-position-y: initial;
	background-size: initial;
	background-repeat-x: initial;
	background-repeat-y: initial;
	background-attachment: initial;
	background-origin: initial;
	background-clip: initial;
	background-color: initial;

	.logo-img {
		width: 385px;
		height: 150px;
		margin: 50px auto 0;
		cursor: pointer;
	}

	.logo-img:hover {
		opacity: 0.8;
	}

	.MuiBox-root.css-binzgt {
		margin-top: 0;
	}

	p {
		margin-top: 24px;
	}

	button {
		margin: 12px 0;
		width: 100%;
	}

	.left-img-div {
		height: 300px;
		width: 300px;
		position: absolute;
		left: 0;
		bottom: 0;

		img {
			width: 100%;
			object-fit: cover;
		}
	}
	.right-img-div {
		height: 300px;
		width: 300px;
		position: absolute;
		right: 0;
		bottom: 0;

		img {
			width: 100%;
			object-fit: cover;
		}
	}

	// width: 100vw;
}
