// Flex Helpers

.flex {
	display: flex;
}

.flex-column {
	display: flex;
	flex-direction: column;
}

.flex-center {
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
}

.flex-btn {
	display: flex;
	justify-content: center;
	align-items: center;
}

.justify-center {
	justify-content: center;
}

.align-center {
	align-items: center;
}

.pointer {
	cursor: pointer;
}

.blue-btn {
	background-color: #2f80ed;
	color: white;
	border: none;
	box-shadow: none;
	margin: 0px $s-2 $s-2 0px;
	padding: 6px 12px;
}

.blue-btn:hover {
	//   transition-duration: 0.85;
	background-color: #0079bf;
	box-shadow: rgba(0, 0, 0, 0.05) 0 5px 30px, rgba(0, 0, 0, 0.05) 0 1px 4px;
	cursor: pointer;
}
.hidden {
	display: none;
}
.overlaySee {
	z-index: 10;
}

.h100 {
	height: 100%;
}
// .grey-btn {

.grey-btn {
	background-color: rgba(9, 30, 66, 0.04);
	border: none;
	color: $b1;
	box-shadow: none;
	margin: 0px $s-2 $s-2 0px;
	padding: 6px 12px;
}

.grey-btn:hover {
	background-color: rgba(9, 30, 66, 0.08);
	cursor: pointer;
}

.modal-top {
	display: flex;
	justify-content: space-between;
	flex-direction: row;
	width: 100%;
	font-weight: 600;
	padding: $s-2 0;
	align-items: center;
	text-align: center;
	height: 40px;
	margin-bottom: $s-2;

	border-bottom: 1px solid rgba(9, 30, 66, 0.12941176470588237);

	button {
		border: none;
		background-color: inherit;
		color: '#5e6c84';
		// font-size: $fs-2;
	}
	button:hover {
		cursor: pointer;
	}

	span {
		width: 90%;
	}

	.close-btn {
		width: 10px;
		height: 16px;
	}

	// span.modal-top-span {
	// 	padding: 0 !important;
	// }
}

//  margin-top and bottom
.m-y-m {
	margin: $s-2 0;
}

// for text areas
.normalize-text {
	white-space: normal;
}

.bold {
	font-weight: bold;
}

.disabled-btn:hover {
	cursor: not-allowed;
}

.preview-cover {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
}

.roundImg {
	border-radius: 50%;
}

.relative {
	position: relative;
}

